<template>
  <swiper class="swiper-container slideMy text-center" :options="swiperOption" ref="swiperContainer">
    <!-- slides -->
    <swiper-slide class="swiper-slide" v-for="(item,index) in slideTopDowns" @click.native="OPEN(item.href)" :key="index">
      <div class="slideMy_LIST">
          <div class="imgs"><img :src="item.image" alt=""></div>
        </div>
    </swiper-slide>
    <div class="swiper-pagination" slot="pagination"></div>
  </swiper>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  props: {
    slideTopDowns: {
      type: Array,
      default: null
    }
  },
  data () {
    let that = this
    return {
      swiperOption: {
        el: that.$refs.swiperContainer,
        autoplay: {
          delay: 3000,
          disableOnInteraction: false
        },
        pagination: {
          el: '.swiper-pagination'
        },
        loop: true,
        observer: true
      }
    }
  },
  methods: {
    // 打开网页
    OPEN (href) {
      if (href.indexOf('#') > -1) {
        this.$router.push(href.replace('#', ''))
      } else if (href.indexOf('http') > -1) {
        if (this.$config.source_style >= 6) {
          window.plus.runtime.openURL(href)
        } else {
          window.open(href)
        }
      } else {
        this.$toast({
          message: '无效的链接'
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .slideMy{
    width: 718px;
    box-sizing: border-box;
    margin: auto;
    border-radius: 10px;
    overflow: hidden;
    margin-top: 20px;
    .slideMy_LIST{
      color: #fff;
      position: relative;
      height: 180px;
      box-sizing: border-box;
    }
    .slideMy_look{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      width: 120px;
      height: 44px;
      line-height: 44px;
      margin: auto;
      border: 2px solid #FFFFFF;
      border-radius: 23px;
    }
    .imgs{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      z-index:-1;
      border-radius: 16px;
      overflow: hidden;
      img{
        width: 100%;
      }
    }
  }
</style>



