<template>
  <div id="money">
    <div class="top flex-row jusB">
      <div class="carTop flex-row jusB f32" style="width: 100%">
        我的财富
        <div class="carInfo flex-row f28">
          <div class="iconSize coin" :class="{isAndroid: $utils.isAndroid()}" @click="setShowIndex">
            <img src="../../assets/img/my/question.png" alt="">
          </div>
          <div class="text">金币余额：</div>
          <div class="redColor">{{couponInfo.coin ? couponInfo.coin: 0}}</div>
        </div>
      </div>
      <!-- <div class="flex-row f24" @click="toPage('/make_money')">掘金宝值多少钱 ></div> -->
    </div>
    <div class="centerLink flex-row">
      <div class="flex" @click="toProfit">
        <div class="f_num f32 nowrap">{{couponInfo.vcoin | setNumber}}</div>
        <div class="f_name f28">掘金宝</div>
      </div>
      <div class="flex"  @click="toProfit">
        <div class="f_num nowrap f32">{{couponInfo.yesterday_wealth_rmb | setNumber}}</div>
        <div class="f_name f28">昨日收入</div>
      </div>
      <div class="flex"  @click="toProfit">
        <div class="f_num f32 nowrap redColor">&yen;{{couponInfo.wealth_rmb | setNumber}}</div>
        <div class="f_name f28">当前价值</div>
      </div>
      <div class="flex"  @click="toProfit">
        <div class="f_num">
          <div class="iconSize income">
            <img src="../../assets/img/my/income.png" alt="收入图标">
          </div>
        </div>
        <div class="f_name f28">我的钱包</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    couponInfo: {
      type: Object,
      default: null
    }
  },
  methods: {
    toProfit () {
      this.$emit('toProfit')
    },
    toPage (url) {
      this.$router.replace({
        path: url,
        query: {
          money: 1
        }
      })
    },
    setShowIndex () {
      this.$emit('setShowIndex')
    }
  },
  filters: {
    setNumber (value) {
      value = value * 1
      if (!isNaN(value)) {
        if (value >= 10000) {
          return  (value / 10000).toFixed(2) + '万'
        } else {
          return value.toFixed(2)
        }
      } else {
        return '0.00'
      }
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss" scoped>
#money{
  width: 710px;
  height: 292px;
  background: #ffffff;
  margin: 0 auto;
  font-size: 30px;
  box-shadow: -1px 0 10px 0 #F5F5F5, 1px 0 10px 0 #CCCCCC, 0 1px 10px 0 #CCCCCC;
  border-radius: 8px;
  color: #232323;
  .top {
    height: 70px;
    margin: 0 10px;
    padding: 0 10px;
    text-align: left;
    border-bottom: 1px solid #f1f1f1;
    .carTop {
      display: flex;
      align-items: flex-end;
    }
    .carInfo{
      font-size: 26px;
      color: #666;
      .redColor {
        color: #FDC223;
        font-size: 28px;
      }
    }
    .coin{
      min-width: 32px;
      width: 31px;
      height: 31px;
      margin:0 10px;
      &.isAndroid {
        margin-top: -6px;
      }
    }
    .link{
      font-size: 24px;
    }
  }
  .centerLink{
    height: 210px;
    font-size: 24px;
    padding: 0 30px;
    justify-content: space-between;
    >div{
      // flex:1;
      flex-flow: column wrap;
      align-items: center;
      .f_num{
        height: 50px;
        display: flex;
        margin: 0 auto;
        align-items: center;
        justify-content: center;
        color: #333;
        margin-bottom: 20px;
        .income{
          min-width: 64px;
          width: 64px;
          height: 64px;
        }
      }
      .redColor{
        color: #f00
      }
      .f_name{
        color: #b4b4b4;
      }
    }
  }
}
</style>



