<template>
  <div class="swiper-no-swiping">
    <div class="slideTopDown_TILE"></div>
    <swiper class="swiper-container slideTopDown text-center" ref="swiper1" :options='swiperOptions'>
      <swiper-slide classs='swiper-slide' v-for='(item, index) in slideTopDowns' :key="index">
        <div class="f24"><span class="userNAME">{{item.nickname}} </span>邀请<span class="cunt">{{item.num}}</span>位好友，他已赚得<span class="prize"> {{item.shortname}}</span></div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  props: {
    slideTopDowns: {
      type: Array,
      default: null
    }
  },
  data () {
    return {
      swiperOptions: {
        direction: 'vertical',
        autoplay: {
          delay: 3000,
          disableOnInteraction: false
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.swiper-no-swiping {
  position: relative;
  .slideTopDown_TILE{
    width: 30px;
    position: absolute;
    left: 40px;
    height: 30px;
    top: 50%;
    transform: translateY(-50%);
    background: url("../../../assets/img/my/MY_copy.png") no-repeat;
    background-size: 100%;
    margin-right: 10px;
  }
  .slideTopDown{
    height: 90px;
    width: 630px;
    margin-left: 80px;
    line-height: 90px;
    text-align: left;
    overflow: hidden;
    .swiper-slide {
    }
    .f24{
      width: 570px;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }
    .userNAME{
      color: #F48B39;
    }
    .cunt,.prize{
      color: #d8ad67;
    }
  }
}
</style>



