        </div>
        <div class="icon_right arrowEl"></div>
      </div>
      <div class="" v-for="(item,index) in trList" v-show="item.txt=== '富豪排行榜'" :key="index" @click="toPage(item.to, item)">
        <div class="img-l"><img :src="item.imgUrl" alt=""></div>
        <div class="texc f28" >
          <div >{{item.txt}}</div>
          <div class="f24 colorE71010">{{item.title}}<span class="num colorCCC">{{item.num}}</span></div>
        </div>
        <div class="icon_right arrowEl"></div>
      </div>
    </div>
    <indexLoop3 v-if="slideTopDowns3.length > 0 && $route.path === '/personal_center' && commonState.login_bol" :slideTopDowns='slideTopDowns3'></indexLoop3>
    <ul class="list f28">
      <li v-for="(item,index) in list"
      :key="index"
