      :class="{'border-1px': !item.border_none, mB20: item.border_none}"
      @click="toPage(item.to, item)">
        <div class="fl icon">
          <img  :src="item.imgUrl" alt="">
        </div>
        <div class="fl">{{item.txt}}</div>
        <div class="fr num mR10 colorCCC">
          <div>{{item.num}}</div>
        </div>
