<script>
import LogIn from './login'
import myMoney from '@/components/personal-center/my_money'
import { mapState, mapMutations } from 'vuex'
import topPop from './top-pop'
import generateCode from './generateCode'
import IndexShare from '@/components/share/IndexShare'
import ShareCommon from '@/components/share/shareCommon'
import indexLoop1 from './index_loop/index_loop1'
import indexLoop2 from './index_loop/index_loop2'
import indexLoop3 from './index_loop/index_loop3'
import download from '@/components/common/download'
import Question from '@/components/personal-center/question'
export default {
  name: 'PersonalCenter',
  computed: mapState({
    commonState: state => state.common,
    share: state => state.share
  }),
  data () {
    return {
      topStatus: '',
      user_icon: '',
      params1: {
        title: '昨天晚上有16万金币，<br>今天没了，怎么回事？',
        content: ['每天凌晨，系统将自动把前一天的金币兑换成掘金宝原始股。具体兑换比例请参考钱包页面。']
      },
      showQuestion1: false,
      params2: {
        title: '何时兑换奖品',
        content: ['平台不直接兑换奖品，倒计时结束上市后，掘金宝原始股交易变现金（实物商品仅作为掘金宝价值参考）。']
      },
      personalInfo: {},
      slideTopDowns1: [],
      slideTopDowns2: [],
      slideTopDowns3: [],
      showQuestion: false,
      newMessageNum: 100,
      appShare: {
        count: 0
      },
      carListLogo: [],
      couponInfo: {},
      trList: [
        {txt: '输入邀请码', to: '/setinvitecode', num: null, imgUrl: require('../../assets/img/my/input.png'), title: '+10掘金宝', needLogin: true},
        {txt: '富豪排行榜', to: '/rich_list', num: null, imgUrl: require('../../assets/img/my/plate.png')},
        {txt: '生成邀请海报', to: '/saveimage', num: null, imgUrl: require('../../assets/img/invite_image.png'), needLogin: true},
        {txt: '生成邀请链接', to: '/short_link', num: null, imgUrl: require('../../assets/img/invite_link.png'), needLogin: true}
      ],
      list: [
        {txt: '历史阅读', to: '/Historys', num: null, imgUrl: require('../../assets/img/personal-center3.png')},
        {txt: '关注', to: '/FocusCenter', num: null, imgUrl: require('../../assets/img/personal-center1.png'), needLogin: true},
        {txt: '收藏', to: '/Collection', border_none: true, num: null, imgUrl: require('../../assets/img/personal-center2.png'), needLogin: true},
        {txt: '我的影评', to: '/movie_comment', num: null, imgUrl: require('../../assets/img/movie/personal_comment.png'), needLogin: true},
        // {txt: '影视提醒', to: '/subscribe', num: null, border_none: true, imgUrl: require('../../assets/img/movie/personal_subscribe.png'), needLogin: true},
        {txt: '活动奖励领取', to: '/activity', border_none: true, num: null, imgUrl: require('../../assets/img/activity.png'), needLogin: true},
        {txt: '常见问题', to: '/task/quesion', num: null, imgUrl: require('../../assets/img/personal-center8.png'), title: '查看新手赚钱秘籍'},
        {txt: '用户反馈', to: '/AwardFeedback', border_none: true, num: null, imgUrl: require('../../assets/img/personal-center4.png')},
        {txt: '设置', to: '/setting', num: null, border_none: true, imgUrl: require('../../assets/img/personal-center9.png')}
      ],
      popupVisible: false,
      nickname: '',     // 用户姓名
      isClear: false,
      isTopPop: false,
      reward_list: [],
      showShare: false,
      shareParams: {},
      frombtnflag: false
    }
  },
  mounted () {
    this.user_icon = this.$utils.cache('user_icon')
    this.$set(this.trList[0], 'title', `+${this.commonState.inviteVcoin * 1}掘金宝`)
  },
  methods: {
    ...mapMutations([
      'common_login_bol',
      'common_login_box',
      'common_index_column',
      'common_set_userInfo',
      'common_get_channel',
      'common_get_recondChannel',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType'
    ]),
    // 监听移动距离
    translateChange (translate) {
      const translateNum = +translate
      this.translate = translateNum.toFixed(2)
      this.moveTranslate = (1 + translateNum / 140).toFixed(2)
    },
    // 监听移动状态听状态
    handleTopChange (status) {
      this.moveTranslate = 2
      this.topStatus = status
    },
    loadTop () {
      this.initData(res => {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded()
        }, 400)
      })
    },
    setShowIndex () {
      this.showQuestion = true
    },
    toDownload () {
      this.$utils.openDownUrl()
    },
    jumpTo (url, flag) {
      if (flag) {
        if (!this.commonState.login_bol) {
          return this.common_login_box(true)
        }
        this.$router.push(url)
      } else {
        this.$router.push(url)
      }
    },
    // 点击显示大礼包弹框
    getBigGifts () {
      this.set_shareType(9)
      this.setShowShare(true)
    },
    // 跳转到详情
    toDetail (item) {
      let name = ''
      switch (item.tag) {
        case 'goods' : name = 'GoodsDetail'
          break
        case 'car' : name = 'CarDetail'
          break
        case 'house' : name = 'HouseDetail'
          break
        default: console.log(item.tag)
      }
      this.$router.push({
        name,
        params: {
          id: item.id
        }
      })
    },
    closeShare () {
      this.showShare = false
    },
    // 微信组件
    shareSuccess (e) {

    },
    getArc (e) {
      if (e) {
        this.shareParams = e
      }
    },
    // 显示app分享
    showAppShare () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      this.appShare.count++
    },
    // 设置分享
    setShareWay (type) {
      if (!this.commonState.login_bol) {
        this.common_login_box(true)
        return
      }
      if (this.$config.source_style >= 6) {
        let index = 0
        let msg = {
          title: this.shareParams.title,
          content: this.shareParams.desc,
          thumbs: [this.shareParams.img_url],
          href: this.shareParams.shareLink
        }
        if (type === 'weixin') {
          index = 1
          msg.type = 'web'
          msg.extra = {
            scene: 'WXSceneSession'
          }
        } else if (type === 'friend') {
          type = 'weixin'
          msg.type = 'web'
          msg.extra = {
            scene: 'WXSceneTimeline'
          }
          index = 2
        } else if (type === 'qq') {
          index = 3
          // msg.type = 'text'
        } else {
          this.$router.push('/saveimage')
          return
        }
        this.commonState.shares[type].send(msg, res => {
          if (index === 3) {
            this.$toast({
              message: '分享成功',
              duration: 1500
            })
          }
          this.setCallBack({type: 'app', way: index})
        }, err => {
          if (err.code === -2) {
            console.log('app分享错误信息：' + JSON.stringify('分享失败'))
            this.$toast({
              message: '已取消分享'
            })
            this.setCallBack({
              type: 'app',
              way: '3',
              success: 2
            })
          } else {
            // this.$toast({
            //   message: '',
            //   duration: 1500
            // })
          }
        })
      } else if (this.$utils.isWx()) {
        if (type === 'weixin' || type === 'qq' || type === 'friend') {
          this.showShare = true
        } else {
          this.$router.push('/short_link')
        }
      } else {
        this.$router.push('/short_link')
      }
    },
    /** 设置分享回调函数
     * @param { Object } obj 'wechat','app'. way 1、微信好友 1、朋友圈 3、qq succes：1 成功 不填 2 为失败 不存在时未知
     */
    setCallBack ({type, way, success}) {
      this.$emit('shareSuccess', {type, way, success})
    },
    // 跳转到收益页面
    toProfit () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      this.toUrl('profit')
    },
    showMessageBox () {

    },
    // 跳转到对应页面
    toUrl (name) {
      this.$router.push({
        name
      })
    },
    // 跳转地址
    toPage (to, item) {
      if (item.txt === '清除缓存') {
        this.isClear = true
        return
      }
      if (to === null || to === '/FocusCenter' || to === '/Collection') {
        if (!this.commonState.login_bol) {
          this.common_login_box(true)
          return
        }
      }
      if (!this.commonState.login_bol && item.txt !== '设置' && item.needLogin) {
        return this.$store.commit('common_login_box', true)
      }
      // if () {
        
      // }
      this.$router.push({path: to})
    },
    // 获取车标
    getCarLogo () {
      this.carListLogo = this.$utils.noRepeatNumber(17, 5)
    },
    getIndex (cb) {
      this.$api.get('MY_INDEX', {}, res => {
        this.personalInfo = JSON.parse(JSON.stringify(res.data))
        // 如果登录状态
        typeof cb === 'function' && cb(res.data)
        if (this.commonState.login_bol) {
          if (res.data.has_inviter === 1) {
            this.$set(this.trList[0], 'title', '')
          } else {
            this.$set(this.trList[0], 'title', `+${this.commonState.inviteVcoin * 1}掘金宝`)
          }
          if (res.data.goods_upgrade === 1) {
            if (res.data.upgrade === 1) {
              let shareSuccessObj = {
                grade: res.data.grade
              }
              this.set_shareSuccessObj({
                type: 11,
                shareSuccessObj
              })
              this.set_shareType(11)
              this.setShowShare(true)
            } else {
              let shareSuccessObj = Object.assign(this.share.shareSuccessObj12, res.data.equivalence_current)
              this.set_shareSuccessObj({
                type: 12,
                shareSuccessObj
              })
              this.set_shareType(12)
              this.setShowShare(true)
            }
          } else {
            if (res.data.upgrade === 1) {
              let shareSuccessObj = {
                grade: res.data.grade
              }
              this.set_shareSuccessObj({
                type: 11,
                shareSuccessObj
              })
              this.set_shareType(11)
              this.setShowShare(true)
            }
          }
        }
      }, res => {
        cb && cb()
      }, res => {
        cb && cb()
      })
    },
    getLoopList () {
      if (this.slideTopDowns1.length === 0) {
        this.$api.get('UPGRADE_BROADCAST', {}, res => {
          this.slideTopDowns1 = res.data
        })
      }
      if (this.slideTopDowns2.length === 0) {
        this.$api.get('SCROLL_USER_PRIZE', {}, res => {
          this.slideTopDowns2 = res.data
        })
      }
      if (this.slideTopDowns3.length === 0) {
        this.$api.get('MY_CHAR', {}, res => {
          this.slideTopDowns3 = res.data
        })
      }
    },
    // 滚动到记录位置
    scrollToHistory () {
      this.$nextTick(() => {
        this.$refs.PersonalCenter.scrollTop = this.$utils.cache('personalHeight') ? this.$utils.cache('personalHeight') : 0
        this.$utils.cache('personalHeight', 0)
      })
      if (!this.commonState.login_bol) {
        this.personalInfo = {}
      }
    },
    initData (cb) {
      this.getCarLogo()
      this.getIndex(cb)
      this.getLoopList()
      this.scrollToHistory()
    },
    showHide () {
      this.isTopPop = !this.isTopPop
    },
    toMygradeinfoPage () {
      this.$router.push({name: 'Mygradeinfo'})
    }
  },
  created () {
    this.initData()
  },
  beforeRouteLeave (to, from, next) {
    this.$utils.cache('personalHeight', this.$utils.getScrollTop(this.$refs.PersonalCenter))
    next()
  },
  activated () {
    this.$store.commit('common_set_imgIndex', 4)
    this.scrollToHistory()
  },
  watch: {
    // 监听如果登录弹框被关闭
    'commonState.login_box' () {
      if (!this.commonState.login_box && this.commonState.login_bol) {
        this.initData()
      }
    },
    // 领取大礼包成功
    'share.showSuccessShare' (flag) {
      if (!flag && this.commonState.userInfo.achieved_gift_bag === 1 && this.share.shareType === 10) {
        this.initData()
      }
    },
    'commonState.inviteVcoin' (vcoin) {
      this.$set(this.trList[0], 'title', `+${vcoin}掘金宝`)
    },
    $route (route) {
      if (route.path === '/personal_center' && this.commonState.login_bol) {
        this.getIndex()
      }
    }
  },
  components: {
    LogIn,
    myMoney,
    topPop,
    generateCode,
    IndexShare,
    ShareCommon,
    indexLoop1,
    indexLoop2,
    indexLoop3,
    download,
    Question
  }
}
</script>

