<template>
  <div class="slideTopDowns1" >
    <swiper class="swiper-container swiper-no-swiping" ref="swiperContainer" :options='swiperOptions'>
      <swiper-slide class="swiper-slide" v-for="(item, index) in slideTopDowns" @click.native="toCars(index)" v-if="item.nickname" :key="index">
        <div class="flex just-sp">
          <div class="sw-img"><img :src="item.goods.thumb" alt=""></div>
          <p class="f24"><span class="name">{{item.nickname}}</span>达到<span class="rank">{{item.grade}}级</span>，赚得<span class="carName">{{item.goods.brand_name}}-{{item.goods.shortname}}</span> <a style="color: #3ca5e9">点击查看></a></p>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  props: {
    slideTopDowns: {
      type: Array,
      default: null
    }
  },
  data () {
    return {
      swiperOptions: {
        autoplay: {
          delay: 3000,
          disableOnInteraction: false
        },
        direction: 'vertical',
        loop: true,
        effect: 'coverflow',
        observer: true,
        progress: true
      }
    }
  },
  methods: {
    toCars (index) {
      let url = this.slideTopDowns[index].url.replace('#', '')
      this.$router.push(url)
    }
  }
}
</script>

<style lang="scss" scoped>
  .slideTopDowns1{
    padding: 0 16px;
    overflow: hidden;
    line-height: 108px;
    box-sizing: border-box;
    height: 108px;
    background: #FFFFFF;
    margin-bottom: 20px;
    box-shadow: 0 1px 4px 0 rgba(174,171,171,0.50);
    .swiper-container {
      width: 100%;
      height: 100%;
      .swiper-slide {
        background:#fff;
      }
    }
    .just-sp{
      justify-content: flex-start;
      width: 690px;
      align-items: center;
      margin: auto;
    }
    .sw-img{
      margin-right: 30px;
      height: 90px;
      margin-top: 9px;
      img{
        height: 100%;
      }
    }
    p{
      line-height: 33px;
      .name,.rank{
        color: #F37F32;
      }
      .carName{
        color: red;
      }
    }
  }
</style>



