<template>
  <div>
    <div id="login" v-show="showPhone">
      <div class="close">
        <img src="../../assets/img/login_close.png" alt="" @click="closeFn">
      </div>
      <div class="hearder text-center f30 mT20">
        登录/注册
      </div>
      <div class="inputOne">
        <input type="tel" class="my-input"
        v-model="phoneNumber"
        placeholder="请输入11位手机号"
        maxlength="11"
        @input="phoneNumber=phoneNumber.replace(/[^\d]/g,'')">
      </div>
      <div class="inputThree clear" v-show="imgCodeBol">
        <input type="text"
        class="my-input fl"
        v-model="imgCode"
        placeholder="请输入图形码"
        maxlength="6">
        <div class="fl imgs">
          <img :src="origin + timestamp" @click="timestamp=new Date().getTime()" alt="图形码加载中">
        </div>
      </div>
      <div class="inputTwo mB25">
        <input class="my-input"
        v-model="codeNumber"
        type="tel"
        placeholder="请输入短信码"
        maxlength="10"
        @input="codeNumber=codeNumber.replace(/[^\d]/g,'')">
        <div class="code f30" v-if="!numBol" @click="getCode">
          获取验证码
        </div>
        <div class="code f30 num" v-if="numBol">
          重新发送<span class="f24">({{numTxt}}s)</span>
        </div>
      </div>
      <div class="btn text-center f30 colorFFF radius10" :class="{active:codeNumber.length > 0 && phoneNumber.length > 0 }" @click="login">
        登录  /  注册
      </div>
      <div class="footer clear text-center mT30 f24">
        <div class="fl">注册即代表同意</div>
        <div class="fl" @click="common_protocol_bol(true)">《掘金宝用户协议》</div>
      </div>
      <transition name="drop">
        <div class="protocol" v-if="protocolBol">
          <login-agreement @close="protocolBol = false"></login-agreement>
        </div>
      </transition>
    </div>
    <div id="chooseType" v-show="showType" @click="closeFn">
      <div class="chooseBox" @click.stop="''">
        <div class="title flex-row">选择登录方式</div>
        <div class="flex-row wechat loginButton" @click="wechatLogin">
          <div class="fixWidth flex-row">
            <div class="iconSize wx">
              <img src="../../assets/img/personal/login_wechat.png" alt="微信登录图标">
            </div>
          </div>
          <div class="text">微信登录&nbsp;&nbsp;</div>
        </div>
        <div class="flex-row phone loginButton" @click="phoneLogin">
          <div class="fixWidth flex-row">
            <div class="iconSize phoneIcon">
              <img src="../../assets/img/personal/login_phone.png" alt="手机号登录图标">
            </div>
          </div>
          <div class="text">手机号登录</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import loginAgreement from '../common/login-agreement'
export default {
  name: 'login',
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    this.origin = this.$config.currentApi + '/v1/passport/captcha?timestamp='
    return {
      showPhone: false, // 是否显示号码输入框
      showType: false, // 显示选择登录类型弹框
      phoneNumber: '',
      isAppLogin: false,
      codeNumber: '',
      protocolBol: false,                    // 协议控制
      numBol: false,                         // 时间显示开关
      numTxt: 0,                             // 时间文本
      timestamp: new Date().getTime(),       // 图片时间戳
      imgCode: '',                           // 图片验证码
      imgCodeBol: false,                     // 是否要图形验证码
      tiemr: null,                           // 定时器
      loginBtn: true                         // 登录按钮的控制
    }
  },
  methods: {
    // 关闭弹框
    closeFn () {
      this.$emit('closeFn')
    },
    // 获取验证码
    getCode () {
      if (!this.$utils.isReg('PHONE_NUMBER', this.phoneNumber)) {
        this.$toast({
          message: '请输入正确手机号',
          position: 'middle',
          duration: 1500
        })
      } else if (this.imgCodeBol && this.imgCode === '') {
        this.$toast({
          message: '请输入图形验证码',
          position: 'middle',
          duration: 1500
        })
      } else {
        let data = {}
        data.mobile = this.phoneNumber
        if (this.imgCodeBol) {
          data.captcha_code = this.imgCode
        }
        this.$api.get('SMSCODE', data, (res) => {
          this.$toast({
            message: res.msg,
            duration: 1500
          })
          this.numBol = true
          this.numTxt = 60
          this.tiemr = setInterval(() => {
            this.numTxt--
            if (this.numTxt === 0) {
              clearInterval(this.tiemr)
              this.numBol = false
            }
          }, 1000)
        }, (res) => {
          if (res.msg === '请输入图形验证码') {
            this.imgCodeBol = true
          }
          if (res.msg === '请输入图形验证码' || res.msg === '图形验证码不正确' || res.msg === '手机验证码不正确') {
            this.timestamp = new Date().getTime()
          }
        })
      }
    },
    getChannel () {
      this.$api.get('MY_CHANNEL', {}, (res) => {
        this.common_get_channel(res.data, 'my_channel')
      })
      this.$api.get('RECOND_CHANNEL', {}, (res) => {
        this.common_get_recondChannel(res.data, 'recond_channel')
      })
    },
    // 显示手机号登录弹框
    phoneLogin () {
      this.showType = false
      this.showPhone = true
    },
    // 微信选择微信登录还是app登录
    wechatLogin () {
      if (this.$config.source_style < 6) {
        this.$utils.wxLogin()
      } else {
        this.appLogin()
      }
    },
    // 用户登录
    login () {
      if (!this.loginBtn) {
        return
      }
      this.loginBtn = false
      setTimeout(() => {    // 过5秒可以重新点
        this.loginBtn = true
      })
      if (!this.$utils.isReg('PHONE_NUMBER', this.phoneNumber)) {
        this.$toast({
          message: '请输入正确手机号',
          position: 'middle',
          duration: 1500
        })
      } else if (this.imgCodeBol && this.imgCode === '') {
        this.$toast({
          message: '请输入图形验证码',
          position: 'middle',
          duration: 1500
        })
      } else if (this.codeNumber === '') {
        this.$toast({
          message: '请输入短信验证码',
          position: 'middle',
          duration: 1500
        })
      } else {
        let data = {}
        data.account = this.phoneNumber
        data.code = this.codeNumber
        if (this.$utils.cache('inviteCodeUser')) {              // 有分享码就传分享码
          data.invitation = this.$utils.cache('inviteCodeUser')
        }
        if (this.imgCodeBol) {
          data.captcha_code = this.imgCode
        }
        // app登录添加推送设备id
        if (this.commonState.pushInfo.clientid && this.commonState.pushInfo.clientid !== null && this.commonState.pushInfo.clientid !== 'null') {
          data.getui_cid = this.commonState.pushInfo.clientid
        }
        this.$api.post('LOGIN', data, (res) => {
          this.$utils.cache('uid', res.data.uid)
          this.$utils.cache('user_token', res.data.user_token)
          this.$utils.cache('nickname', res.data.nickname)
          this.$utils.cache('first_login', res.data.first_login)
          this.$utils.cache('inviteCode', res.data.invitation)
          this.$utils.cache('user_icon', res.data.avatar)
          this.$utils.cache('user_city', res.data.city)
          this.$utils.cache('userInfo', JSON.stringify(res.data))
          this.setShowGift()
          clearInterval(this.tiemr)
          this.numBol = false
          this.numTxt = 0
          this.common_index_column(true)
          this.common_set_userInfo(res.data)
          this.common_login_bol(true)
          this.getChannel()
          this.$utils.cache('inviteCodeUser', null)    // 用完分享码后删除
          this.$emit('closeFn') // 关闭登录框
          this.phoneNumber = '' // 输入框边控
          this.codeNumber = ''
          this.imgCode = ''
          this.$toast({
            message: res.msg,
            position: 'middle',
            duration: 3000
          })
          this.loginBtn = true
        }, (res) => {
          this.loginBtn = true
          if (res.msg === '请输入图形验证码') {
            this.imgCodeBol = true
          }
          if (res.msg === '请输入图形验证码' || res.msg === '图形验证码不正确' || res.msg === '手机验证码不正确') {
            this.timestamp = new Date().getTime()
          }
        })
      }
    },
    // 是否显示大礼包
    setShowGift () {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      this.timer = setTimeout(() => {
        // 新用户，未领取大礼包，今天是否提醒过
        let showBigGifts = this.$utils.cache('showBigGifts') ? JSON.parse(this.$utils.cache('showBigGifts')) : null
        // 如果缓存存在并且缓存时间戳小于当前时间戳可弹起
        if (showBigGifts && showBigGifts.timeStamp < this.$utils.getTimeStamp()) {
          this.$utils.cache('showBigGifts', null)
          showBigGifts = null
        }
        if (this.commonState.userInfo.is_new === 1 && this.commonState.userInfo.achieved_gift_bag === 0 && !showBigGifts) {
          this.$store.commit('setShowShare', true)
          this.$store.commit('set_shareType', 9)
          this.$utils.cache('showBigGifts', JSON.stringify({
            timeStamp: this.$utils.getTodayStamp()
          }))
        }
      }, 1000)
    },
    // app登录
    appLogin () {
      var that = this
      if (this.$config.source_style === 6 || this.$config.source_style === 7) {
        if (window.plus) {
          setLogin()
        } else {
          document.addEventListener('plusready', function () {
            setLogin()
          })
        }
      }
      function setLogin () {
        window.plus.oauth.getServices(function (service) {
          var authWeiXin = {}
          for (let i = 0; i < service.length; i++) {
            if (service[i].id === 'weixin') {
              authWeiXin = service[i]
            }
          }
          if (window.plus.runtime.isApplicationExist({ pname: 'com.tencent.mm', action: 'weixin://' })) {
            authWeiXin.authorize(function (e) {
              let data = {
                type: 2, // app登录
                code: e.code
              }
              // app登录添加推送设备id
              if (that.commonState.pushInfo && that.commonState.pushInfo.clientid && that.commonState.pushInfo.clientid !== null && that.commonState.pushInfo.clientid !== 'null') {
                data.getui_cid = that.commonState.pushInfo.clientid
              }
              console.log(JSON.stringify(data))
              that.$api.post('WECHAT_LOGIN', data, function (res) {
                that.$utils.cache('uid', res.data.uid)
                that.$utils.cache('user_token', res.data.user_token)
                that.$utils.cache('nickname', res.data.nickname)
                that.$utils.cache('first_login', res.data.first_login)
                that.$utils.cache('user_icon', res.data.avatar)
                that.$utils.cache('inviteCode', res.data.invitation)
                that.$utils.cache('userInfo', JSON.stringify(res.data))
                that.common_index_column(true)
                that.common_login_bol(true)
                that.common_set_userInfo(res.data)
                that.setShowGift()
                // 登录成功获取频道
                that.getChannel()
                that.$toast({
                  message: '登录成功',
                  position: 'middle',
                  duration: 1500,
                  icon: 'icon icon-success'
                })
                that.$emit('closeFn') // 关闭登录框
              }, function (e) {
                if (e.code !== 0) {
                  that.$toast({
                    message: '登录失败',
                    position: 'middle',
                    duration: 1500
                  })
                  that.$emit('closeFn') // 关闭登录框
                }
              })
            }, function (e) {
              that.closeFn('closeFn')
            }, {scope: 'snsapi_userinfo', state: 'authorize test'})
          } else {
            that.$toast('请安装微信')
            that.closeFn()
          }
        })
      }
    },
    ...mapMutations([
      'common_login_bol',
      'common_index_column',
      'common_set_userInfo',
      'common_protocol_bol',
      'common_get_channel',
      'common_get_recondChannel'
    ])
  },
  destroyed () {
    clearInterval(this.tiemr)
  },
  watch: {
    'commonState.login_box' (flag) {
      this.imgCodeBol = false
      this.imgCode = ''
      if (flag) {
        if (this.$config.source_style >= 6 || this.$utils.isWx()) {
          this.showType = true
          this.showPhone = false
        } else {
          this.showPhone = true
          this.showType = false
        }
      }
    }
  },
  components: {
    loginAgreement
  },
  mounted () {

  }
}

</script>

<style lang="scss" scoped>
#login{
  width: 464px;
  padding: 50px;
  position: relative;
  img{
    width: 100%;
    height: 100%;
  }
  .protocol{
    background: #fff;
    position: fixed;
    width: 750px;
    height: 100vh;
    top: -390px;
    left: -94px;
    box-sizing: border-box;
  }
  .close{
    width: 24px;
    height: 24px;
    position: absolute;
    right: 20px;
    top: 20px;
  }
  .footer{
    div:nth-of-type(1){
      color: #AEAEAE;
      margin-left: 45px;
    }
    div:nth-of-type(2){
      color: #ED8677;
    }
  }
  .btn{
    width: 100%;
    height: 80px;
    background: #ED8677;
    line-height: 80px;
  }
  .btn.active{
    background: #f65959
  }
  .code{
    color: #58CBEA;
    position: absolute;
    right: 15px;
    top: 28px;
    width: 195px;
    text-align: center;
    border-left: 1px solid #EDEDED;
    padding-left: 10px;
    line-height: 50px;
  }
  .num{
    color: #aaa;
  }
  .inputThree{
    margin-bottom: 25px;
    .imgs{
      height: 90px;
      width: 200px;
      margin-left: 20px;
    }
    input{
      width: 230px;
    }
  }
  .inputTwo{
    position: relative;
  }
  .inputOne{
    margin-top: 45px;
    margin-bottom: 25px;
  }
  input{
    border: none;
    -webkit-appearance: none;
    border: 1px solid #f0f0f0;
    line-height: 82px;
    width: 460px;
    border-radius: 41px;
    text-indent: 20px;
    font-size: 30px;
  }
  input:focus{
    outline: none;
  }
  .my-input::-webkit-input-placeholder {
    color: #afafaf;
    font-size: 30px;
  }
}
// 登录弹框
#chooseType{
  position: absolute;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, .56);
  .chooseBox{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #FFFFFF;
    box-shadow: 0 2px 8px 0 rgba(135,134,134,0.50);
    border-radius: 10px;
    width: 616px;
    height: 496px;
    box-sizing: border-box;
    padding: 0 30px;
    .title{
      height: 160px;
    }
    .loginButton{
      height: 90px;
      border-radius: 10px;
      margin-bottom: 50px;
      background: #0FB91A;
      box-sizing: border-box;
      color: #fff;
      font-size: 30px;
      .fixWidth{
        width: 80px;
        padding-right: 20px;
      }
      .wx{
        width: 60px;
        height: 48px;
      }
      .phoneIcon{
        width: 32px;
        height: 48px;
      }
    }
    .phone{
      border: 1px solid #dbdbdb;
      background: #fff;
      color: #979797;
    }
  }
}
</style>



