<template>
  <div class="top-pop text-center" style="z-index: 15">
      <div></div>
    <div class="conte">
      <div class="title">
        赚掘金宝(原始股)
        <div class="close" @click="outVanish"></div>
      </div>
      <p class="f28 content">新用户点击你的分享页面并登录，<br>你就能赚掘金宝，省心省力！</p>
      <div class="conte_img flex">
        <div >
          <div class="one">
            <div class="iconSize">
              <img src="../../assets/img/my/pop_one.png" width="93" alt="页面1">
            </div>
          </div>
          <div class="f28 tex">分享任意页面</div>
        </div>
        <div class="two"></div>
        <div>
          <div class="three">
            <div class="iconSize">
              <img src="../../assets/img/my/pop_three.png" width="93" alt="页面3">
            </div>
          </div>
          <div class="f28 tex">新用户进入分享页,你可得掘金宝</div>
        </div>
      </div>
      <div class="click" @click="outVanish">知道了</div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['isTopPop'],
  methods: {
    outVanish () {
      this.$emit('change')
    }
  }
}
</script>

<style lang="scss" scoped>
.top-pop {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0,0,0,0.50);
}
.conte{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 600px;
  height: 800px;
  background: #fff;
  box-shadow: 0 2px 8px 0 rgba(74,74,74,0.50);
  border-radius: 16px;
  z-index: 100;
  overflow: hidden;
  .title{
    background-image: linear-gradient(-210deg, #FFD500 0%, #FFAA00 100%);
  }
  .conte_img{
    justify-content: space-between;
    padding:  0 50px 0 50px;
    > div {
      display: flex;
      flex-flow: column wrap;
      justify-content: space-between;
      align-items: center;
      height: 390px;
    }
    .one{
      .iconSize {
        width: 198px;
        height: 316px;
      }
    }
    .two{
      width: 57px;
      height: 44px;
      min-width: 57px;
      background: url("../../assets/img/my/pop_two.png") no-repeat;
      background-size: 100%;
      margin-top: 130px;
    }
    .three{
      width: 175px;
      height: 297px;
      background-size: 100%;
      .iconSize {
        width: 172px;
        height: 306px;
      }
      .tex{
        width: 224px;
      }
    }
    .tex{
      // width: 224px;
      width: 224px;
      line-height: 40px;
      color: #ffac00;
    }
  }
  .content{
    margin: 23px 50px 12px;
    line-height: 40px;
    font-size: 28px;
    color: #232323;
  }
  .title{
    height: 120px;
    line-height: 120px;
    font-size: 44px;
    color: white;
    font-weight: 600;
    position: relative;
    .close {
      position: absolute;
      width: 46px;
      height: 46px;
      background: url('../../assets/img/task/close.png') no-repeat center;
      background-size: cover;
      right: 36px;
      top: 27px;
    }
  }
  .click{
    background: #FFAC00;
    width: 500px;
    height: 80px;
    line-height: 80px;
    border-radius: 80px;
    margin: auto;
    color: #fff;
    margin-top: 21px;
  }
}
</style>



