<template>
  <div class="question" @touchmove.prevent="" style="z-index: 11">
    <div class="q-content">
      <div class="title" v-html="config.title"></div>
      <div class="q-content-outer">
        <div class="q-content-text" v-for="(item, index) in config.content" :key="index">{{item}}</div>
      </div>
      <div class="close" @click="closeFn"></div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    config: {
      type: Object
    }
  },
  data () {
    return {

    }
  },
  methods: {
    closeFn () {
      this.$emit('closeFn')
    }
  }
}
</script>
<style lang="scss" scoped>
.question {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba($color: #000000, $alpha: .5);
  .q-content {
    background: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 600px;
    border-radius: 20px;
    padding-bottom: 120px;
    margin-top: -60px;
    .title {
      padding: 38px 30px 30px;
      line-height: 56px;
      font-size: 36px;
      font-weight: 900;
      text-align: center;
      color: #fff;
      border-radius: 20px 20px 0 0;
      overflow: hidden;
      background: linear-gradient(to right, #FFAA00 0%, #FFD500 100%);
    }
    .q-content-text{
      font-size: 32px;
      padding: 0 30px;
      line-height: 45px;
      margin: 30px 0;
    }
    .close {
      width: 60px;
      height: 60px;
      background: url('../../assets/img/task/close.png') no-repeat center;
      background-size: 45px 45px;
      position: absolute;
      bottom: -100px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
</style>



