<template>
  <div id="generateCode">
    <div class="g-top f20">你的邀请码</div>
    <div class="g-code flex-row f46" v-if="commonState.userInfo.invitation" :class="{smallSize: commonState.userInfo.invitation.length > 7}">{{commonState.userInfo.invitation ? commonState.userInfo.invitation : '******'}}</div>
    <div class="g-button flex-row jusC">
      <div class="button clipbtn" @click="copyCode" :data-clipboard-text="copyText">复制</div>
      <!-- <div class="button" v-else @click="generateCode">生成邀请码</div> -->
    </div>
  </div>
</template>
<script>
import {mapState, mapMutations} from 'vuex'
import Clipboard from 'clipboard'
export default {
  data () {
    return {
      copyText: ''
    }
  },
  computed: mapState({
    commonState: state => state.common
  }),
  methods: {
    ...mapMutations([
      'common_login_box'
    ]),
    // 生成邀请码
    generateCode () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
    },
    // 复制邀请码
    copyCode () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      if (Clipboard.isSupported()) {
        let clb = new Clipboard('.clipbtn')
        clb.on('success',  (e) => {
          this.$toast({
            message: '复制成功,请发送给好友',
            position: 'middle',
            duration: 1500,
            iconClass: 'icon icon-success'
          })
          clb.destroy()
        })
        clb.on('error',  (e) => {
          this.$toast({
            message: '复制链接失败了',
            position: 'middle',
            duration: 1500,
            iconClass: 'icon icon-success'
          })
          clb.destroy()
        })
      }
    }
  },
  created () {
    this.copyText = `${this.commonState.userInfo.invitation}`
  },
  watch: {
    // 登录窗关闭
    'commonState.login_box': function (flag) {
      if ((!flag) && this.commonState.login_bol) {
        this.copyText = `${this.commonState.userInfo.invitation}`
      }
    }
  }
}
</script>
<style lang="scss" scoped>
#generateCode{
  /*width: 631px;*/
  width: 200px;
  height: 190px;
  display: flex;
  justify-content: center;
  flex-flow: column wrap;
  position: relative;
  .g-top{
    height: 50px;
    width: 132px;
    text-align: center;
    color: #fff;
    line-height: 40px;
    background:url("../../assets/img/my/myASK.png") no-repeat;
    background-size: 100%;
    margin-bottom: 20px;
  }
  .g-code{
    font-family: HelveticaNeue-Bold;
    font-size: 50px;
    color: #F0C070;
    font-weight: 600;
    // letter-spacing: 2.47px;
    justify-content: flex-start;
  }
  .g-code.smallSize {
    font-size: 45px;
  }
  .g-button{
    position: absolute;
    left: 140px;
    white-space: nowrap;
    top: 36px;
    .button{
      font-size: 24px;
      height: 34px;
      padding: 0 10px;
      text-align: center;
      color: #ccc;
      line-height: 34px;
      border-radius: 34px;
      border: 1px solid #232323;
    }
  }
}
</style>



